        <text class="subtitle">‰∏ä‰º†Èü≥È¢ëÊñá‰ª∂ÔºåÂàÜÊûêËØ≠Èü≥ÊÉÖÁª™</text>
      </view>
      
      <!-- Èü≥È¢ë‰∏ä‰º†Âå∫Âüü -->
      <view class="upload-section">
        <view class="upload-area" @click="chooseAudio" v-if="!audioFile">
          <text class="upload-icon">üéµ</text>
          <text class="upload-text">ÁÇπÂáªÈÄâÊã©Èü≥È¢ëÊñá‰ª∂</text>
