      </view>
      
      <!-- ÈîôËØØ‰ø°ÊÅØ -->
      <view class="error-section" v-if="errorMessage">
        <text class="error-text">{{ errorMessage }}</text>
        <button class="btn btn-secondary" @click="clearError">ÈáçËØï</button>
      </view>
    </view>
  </view>
</template>

<script>
import { config } from '@/utils/config.js'

export default {
  data() {
    return {
      audioFile: null,
      analysisResult: null,
      analyzing: false,
      errorMessage: ''
    }
  },
  
  methods: {
    goBack() {
      uni.navigateBack()
    },
    
    // Ëé∑ÂèñÂü∫Á°ÄURL
    getBaseUrl() {
      return config.BASE_URL
    },
    
    chooseAudio() {
      // ‰ºòÂÖà‰ΩøÁî® chooseAudioÔºàÂæÆ‰ø°Â∞èÁ®ãÂ∫èÂéüÁîüAPIÔºâ
      if (uni.chooseAudio && typeof uni.chooseAudio === 'function') {
        uni.chooseAudio({
          count: 1,
          success: (res) => {
            this.audioFile = {
              tempFilePath: res.tempFilePath,
              name: res.tempFilePath.split('/').pop() || 'audio.mp3',
              size: res.size || 0
            }
            this.clearError()
          },
          fail: (err) => {
            console.error('chooseAudio ÈÄâÊã©Èü≥È¢ëÂ§±Ë¥•:', err)
            if (err.errMsg && err.errMsg.includes('cancel')) {
              // Áî®Êà∑ÂèñÊ∂àÔºå‰∏çÊòæÁ§∫ÈîôËØØ
              return
            }
            // chooseAudio Â§±Ë¥•Êó∂ÔºåÂ∞ùËØï‰ΩøÁî® chooseMedia
            this.chooseAudioWithChooseMedia()
          }
        })
      } else {
        // chooseAudio ‰∏çÂèØÁî®Êó∂Ôºå‰ΩøÁî® chooseMedia
        this.chooseAudioWithChooseMedia()
      }
    },
    
    // ‰ΩøÁî® chooseMedia ÈÄâÊã©Èü≥È¢ëÔºàÂ§áÈÄâÊñπÊ°àÔºâ
    chooseAudioWithChooseMedia() {
      if (uni.chooseMedia && typeof uni.chooseMedia === 'function') {
        uni.chooseMedia({
          count: 1,
          mediaType: ['audio'],
          sourceType: ['album', 'camera'],
          success: (res) => {
            console.log('chooseMedia ÈÄâÊã©Èü≥È¢ëÊàêÂäü:', res)
            if (res.tempFiles && res.tempFiles.length > 0) {
              const file = res.tempFiles[0]
              this.audioFile = {
                tempFilePath: file.tempFilePath,
                name: file.name || file.tempFilePath.split('/').pop() || 'audio.mp3',
                size: file.size || 0
              }
              this.clearError()
            } else {
              this.errorMessage = 'Êú™ÈÄâÊã©Èü≥È¢ëÊñá‰ª∂'
              uni.showToast({
                title: 'Êú™ÈÄâÊã©Èü≥È¢ëÊñá‰ª∂',
                icon: 'none'
              })
            }
          },
          fail: (err) => {
            console.error('chooseMedia ÈÄâÊã©Èü≥È¢ëÂ§±Ë¥•:', err)
            if (err.errMsg && err.errMsg.includes('cancel')) {
              return
            }
            this.errorMessage = 'ÈÄâÊã©Èü≥È¢ëÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
            uni.showToast({
              title: 'ÈÄâÊã©Èü≥È¢ëÂ§±Ë¥•',
              icon: 'none'
            })
          }
        })
      } else {
        // ‰∏§ÁßçÊñπÊ≥ïÈÉΩ‰∏çÂèØÁî®
        uni.showToast({
          title: 'ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅÈü≥È¢ëÈÄâÊã©',
          icon: 'none',
          duration: 2000
        })
      }
    },
    
    removeAudio() {
      this.audioFile = null
      this.analysisResult = null
      this.clearError()
    },
    
    formatFileSize(size) {
      if (size === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
      const i = Math.floor(Math.log(size) / Math.log(k));
      return parseFloat((size / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },
    
    async analyzeAudio() {
      if (!this.audioFile) {
        this.errorMessage = 'ËØ∑ÂÖàÈÄâÊã©Èü≥È¢ëÊñá‰ª∂'
        return
      }
      
      this.analyzing = true
      this.clearError()
      
      try {
        const baseUrl = this.getBaseUrl()
        const response = await uni.uploadFile({
          url: baseUrl + '/api/nlp/emotion/analyze/audio',
          filePath: this.audioFile.tempFilePath,
          name: 'audio',
          success: (res) => {
            try {
              const data = JSON.parse(res.data)
              if (data && data.code === 200) {
                this.analysisResult = data.data
                uni.showToast({
                  title: 'ÂàÜÊûêÂÆåÊàê',
                  icon: 'success'
                })
              } else {
                this.errorMessage = data?.message || 'ÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
              }
            } catch (e) {
              console.error('ÂìçÂ∫îËß£ÊûêÂ§±Ë¥•:', e)
              this.errorMessage = 'ÂìçÂ∫îËß£ÊûêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
            }
          },
          fail: (err) => {
            console.error('Èü≥È¢ë‰∏ä‰º†Â§±Ë¥•:', err)
            this.errorMessage = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ËøûÊé•ÂêéÈáçËØï'
          }
        })
      } catch (error) {
        console.error('Èü≥È¢ëÂàÜÊûêÂ§±Ë¥•:', error)
        this.errorMessage = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ËøûÊé•ÂêéÈáçËØï'
      } finally {
        this.analyzing = false
      }
    },
    
    clearError() {
      this.errorMessage = ''
    }
  }
}
</script>

